import React, {useState} from 'react';
import '../Css/home.css';
import img2 from '../images/users.jfif';
import img3 from '../images/users1.png';

const Home = () => {
    
   

    const [userData, setuserData] = useState([])
    const [name, setName] = useState('');
    const [surname, setSurname] = useState('');
    const [age, setAge] = useState('');
    const [location, setLocation] = useState('');

    const AddUsers = (name, surname)=>{
        setuserData([...userData,{
        _name:name,
        _surname:surname,
        _age:age,
        _location:location
    }])
}

//Function to accept users
const submitData = (e) => {
    e.preventDefault()
    AddUsers(name, surname)
}

    return (
        // Column 1
        <div class="section group">
	<div class="col span_1_of_2">
	<div className="site-info">
            <div className="title">
                Student Registration Interface
            </div>
            <div className="sub-title">
                Click the area below to register..
            </div>
            <div class="box">
            <input type="file" id="file" name="image" ></input>
            <img src={img2} alt="" width="100%" height="100%"/>

            {/* Insert Form */}
            <form onSubmit={submitData}> 
            <input 
            type="text" 
            name="name" 
            value={ name } 
            placeholder="Enter your name" 
            onChange={ (e) => setName(e.target.value)}
            />

            <input 
            type="text" 
            name="surname" 
            value={ surname } 
            placeholder="Enter your surname"
            onChange={ (e) => setSurname(e.target.value)}
            />
           

            <input 
            type="number" 
            name="age" 
            value={ age } 
            placeholder="Enter your age"
            onChange={ (e) => setAge(e.target.value)}
            />
            

            <input 
            type="text" 
            name="location" 
            value={ location } 
            placeholder="Enter your location"
            onChange={ (e) => setLocation(e.target.value)} 
            />
            

            <button type="submit">Register</button>
            </form>

            </div>
        </div>
	</div>


    {/* Column 2 */}
	<div class="col span_1_of_2">
	<div className="data">Student Registration Data</div>
    <div className="sub-title">
                Click below to see registered students..
            </div>
            <div class="box">
            <input type="file" id="file" name="image" ></input>
            <img src={img3} alt="" width="100%" height="100%"/>

                {/* Display Students */}
                <div className="post-students">
                
                    <h5 className="Student">Registered Students:</h5>
                    {userData.map( e=><p>{e._name}  {e._surname}</p>)}
                    
                
                </div>
            
            </div>
	</div>
</div>
        
        
    )
}

export default Home;
